<script>
	import Config from '../../config.js'
	import docTable from '../common/doc.vue'
	export default {
		name: 'masks',
		components: {docTable},
		data() {
			var validateCode = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                }else {
                    callback();
                }
            };
            var validateName = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                }else {
                    callback();
                }
            };
            var validateAddress = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                }else {
                    callback();
                }
            };
            var validatePhone = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                }else {
                    callback();
                }
            };
            var validateEmail = (rule, value, callback) => {
                if (value === '') {
		            callback(new Error(''));
		        } else {
			        var reg=/^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
			        if(!reg.test(value)){
			            callback(new Error(''));
			        }else{
			        	callback();
			        }
		        }
            };
            var validateName = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                }else {
                    callback();
                }
            };
            var validateUnit = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                }else {
                    callback();
                }
            };
            var validateItemtype = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                }else {
                    callback();
                }
            };
            var validateType = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                }else {
                    callback();
                }
            };
            var validateBasislist = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                }else {
                    callback();
                }
            };
			//
			var validateItemdata = (rule, value, callback) => {
                if (this.WORKPLAN.ITEMTYPE === undefined || this.WORKPLAN.ITEMTYPE === '' || this.WORKPLAN.ITEMTYPE === null) {
                    callback(new Error(''));
                }else {
                    callback();
                }
            };
			return {
				docParm: {
					'model': 'new',
					'recordid': 1,
					'userid': 1,
					'username': '',
					'deptid': 1,
					'deptfullname': '',
					'appname': '',
					'appid': 1
				},
				basic_url: Config.dev_url,
				showEdit: [], //
		        showBtn: [],
		        showBtnOrdinary: true,
		        fullHeight: document.documentElement.clientHeight - 210+'px',//
				value: '',
				assignshow:false,//
				options: [{
					value: '1',
					label: ''
				}, {
					value: '0',
					label: ''
				}],
				loadSign:true,//
				commentArr:{},
				selUser:[],
				productList:[],
				edit: true, //
				col_but1: true,
				col_but2: true,
				show: false,
				modify:false,
				isok1: true,
				isok2: false,
				down: true,
				up: false,
				addtitle:true,//titile
				modifytitle:false,//titile
				viewtitle: false, //title
				noviews: true, //
				views: false,
				activeName: 'first',//tabs
				activeNames: ['1','2','3','4','5','6','7'],//
				// labelPosition: 'right', //
				dialogVisible: false, //
				dialogVisible2: false, //
				dialogVisible3: false, //
				dialogVisible4: false, //
				diaVisCustom:false,//
				searchList: { //
					S_NUM: '',
					S_NAME: '',
					VERSION: '',
					DEPARTMENT: '',
					RELEASETIME: '',
					STARTETIME: '',
					STATUS: '',
				},
				dataList:[{
					name:'',
					description:''
				}],
				search:'',
				selectData:[],
				standardList: [],//
				projectList: [],//
				fileList:[],//
				page: { //
					currentPage: 1,
					pageSize: 10,
					totalCount: 0
				},
				WORKPLAN:{},
				inspectionList_child: {
					 WORLPLANLINE_BASISList:[],
					 WORLPLANLINE_PROJECTList:[]
				},
				
				rules: {
					PROP_UNIT:[{required: true,validator: validateUnit, trigger: 'change'}],// 
					basisList:[{required: true,validator: validateBasislist,trigger: 'change'}],//
					DESCRIPTION:[{required: true,trigger: 'blur',validator: this.Validators.isFillTips}],

					TYPE:[{required: true,message: '', trigger: 'change'}],

					COMPACTOR:[
						{required: true, trigger: 'blur',message: '' },
						{trigger: 'blur', validator: this.Validators.isNickname},
					],//
					C_PERSON:[
						{required: true, trigger: 'blur',message: '' },
						{trigger: 'blur', validator: this.Validators.isNickname},
					],//
					APPRPERSON:[
						{required: true, trigger: 'blur',message: '' },
						{trigger: 'blur', validator: this.Validators.isNickname},
					],//

					CONTACT_ADDRESS:[{required: true,trigger:'blur',validator: validateAddress,}],
					PHONE:[{required: true,trigger: 'blur',validator: validatePhone,}],
					EMAIL:[{required: true,trigger: 'blur',validator:validateEmail,}],
       				ITEMTYPE:[{required: true,validator: validateItemdata}],// 
       				YEAR: [{type: 'string',required: true,message: '',trigger: 'change' }],//
       				REPORTDATE: [{type: 'string',required: true,message: '',trigger: 'change'}],//
       				// 
					ITEM_NAME:[{required: true,message: '',trigger: 'blur'}], // 
					MODEL:[{required: true,message: '',trigger: 'blur'}],
					REMARKS:[{required: true,message: '',trigger: 'blur'}],
				},
				//tree
				resourceData: [], //
				worlplanlist: [], //				
				basisList: [], //
				proTestList: [], //
				isEditList: false,  //
				editPlan: {},  //
				frontId: 1, //
				index:0,
				views:true,
				dept:true,
				categoryList:[],
				proindex:0,//
				deptindex:0,//
				falg:false,
				customerList:[],//custom
				deptnum:'',//
				requestnum:0,//
				itemtypenum:'',//
				pronamenum:'',//
				basisnums:'',//
				noedit:false
			};
		},
		methods: {
			download() {
				var url = this.basic_url + '/api-apps/app/workplan/importExcTemplete/WORLPLANLINE?access_token='+sessionStorage.getItem('access_token');
				var xhr = new XMLHttpRequest();
					xhr.open('POST', url, true);
					xhr.responseType = "blob";
					xhr.setRequestHeader("client_type", "DESKTOP_WEB");
					xhr.onload = function() {
						if (this.status == 200) {
							var blob = this.response;
							var objecturl = URL.createObjectURL(blob);
							window.location.href = objecturl;
						}
					}
					xhr.send();
			},
			uploadUrl(){
                var url = this.basic_url +'/api-apps/app/workplan/importExc/WORLPLANLINE?access_token='+sessionStorage.getItem('access_token');
                return url;
            },
			exportData() {
           		var url = this.basic_url + '/api-apps/app/workplan/exportExc/WORLPLANLINE?access_token='+sessionStorage.getItem('access_token');
          		var xhr = new XMLHttpRequest();
            	xhr.open('POST', url, true);
            	xhr.responseType = "blob";
            	xhr.setRequestHeader("client_type", "DESKTOP_WEB");
            	xhr.onload = function() {
                	if (this.status == 200) {
						var filename = "worlplanline.xls";
						var blob = this.response;
						var link = document.createElement('a');
						var objecturl = URL.createObjectURL(blob);
						link.href = objecturl;
						link.download = filename;
						link.click();
                	}
            	}
            	xhr.send();
			},
			resetBasis(){
				this.dialogVisible = false;
				this.resetbtn();
				this.standardList = [];
			},
			//
			getCompany() {
				var type = "2";
				var url = this.basic_url + '/api-user/depts/treeByType';
				this.$axios.get(url, {
					params: {
						type: type
					},
				}).then((res) => {
					this.selectData = res.data;
				});
			},
			//
			resetbtn(){
				this.searchList = {
					S_NUM:'',
					S_NAME:'',
					VERSION:'',
					DEPARTMENT:'',
					RELEASETIME:'',
					STARTETIME:'',
					STATUS:'',
					P_NUM:'',
					DEPTID:'',
					P_NAME:'',
					VERSION:'',
					STATUS:'',
				};
			},
			//
			rowClass({ row, rowIndex}) {
			    return 'text-align:center'
			},
			toNum(str) {
				return str.replace(/\,|\/g, "");
			},
			//
			toPrice(item){
				var money = document.getElementById("cost").value;
				var num = parseFloat(this.toNum(money)).toFixed(2).toString().split(".");
				num[0] = num[0].replace(new RegExp('(\\d)(?=(\\d{3})+$)','ig'),"$1,");
				item.CHECKCOST = num.join(".");
			},
			//
			changeEdit(row){
				this.$nextTick(function(){
					row.isEditing = !row.isEditing;
				});
			},
			//
			prodeptbtn(item){
				// this.requestData();
				this.requestDeptname();
				this.diaVisCustom = true;
				// this.$emit('request');
				this.proindex = item;
				this.requestnum = '3';
				this.deptnum = '1';
			},
			//
			getdeptbtn(item){
				// this.requestData();
				this.requestDeptname();
				this.diaVisCustom = true;
				// this.$emit('request');
				this.deptindex = item;
				this.requestnum = '3';
				this.deptnum = '2';
			},
			//
			adddeptname(){
				if(this.selUser.length == 0){
					this.$message({
						message: '',
						type: 'warning'
					});
				}else if(this.selUser.length > 1){
					this.$message({
						message: '',
						type: 'warning'
					});
				}else{
					if(this.deptnum == '1'){
						this.proindex.V_NAME = this.selUser[0].NAME;
					}else if(this.deptnum == '2'){
						this.deptindex.SJ_NAME = this.selUser[0].NAME;
					}
					this.diaVisCustom = false;
					// this.$emit('request');
					this.requestDeptname();
				}
			},
			
			//
			delPlan(index,row,TableName,delList){
				if(row.ID){
					var url = this.basic_url +'/api-apps/app/workplan/' + TableName +'/' + row.ID;
					this.$confirm('', '', {
						confirmButtonText: '',
						cancelButtonText: '',
					}).then(({
						value
					}) => {
						this.$axios.delete(url, {}).then((res) => {
							if(res.data.resp_code == 0){
									this[delList].splice(index,1);
							}else{
								this.$message({
									message: res.data.resp_msg,
									type: 'error'
								});
							}
						}).catch((err) => {
							this.$message({
								message: '',
								type: 'error'
							});
						});
					}).catch(() => {

					});
				}else{
						this[delList].splice(index,1);
				}
			},
			//
			assign(item){
				var dataid = item.ID;
				if(item.ISCREATED == 1){
					this.$message({
						message: '',
						type: 'warning'
					});
					// return;
				}else{
					this.$axios.get(this.basic_url + '/api-apps/app/workplan/operate/createWorkNotice?ID=' + dataid, {}).then((res) => {
					if(res.data.resp_code == 0) {
						this.$message({
							message: '',
							type: 'success'
						});
					}else{
						this.$message({
							message: res.data.resp_msg,
							type: 'warning'
						});
					}
				}).catch((err) => {
					this.$message({
						message: '',
						type: 'error'
					});
				});
				}
				
