				$(".mask_div").css("width", "80%");
				$(".mask_div").css("height", "80%");
				$(".mask_div").css("top", "100px");
			},
			save(opt) {
				this.$refs.WORKPLAN.validate((valid) => {
					if(!valid && opt == 'docUpload'){
						this.$message({
							message: '',
							type: 'warning'
						});
					}
					if (valid) {
						if(this.worlplanlist.length>0){
							for(var i=0;i<this.worlplanlist.length;i++){
								if(!this.worlplanlist[i].WORLPLANLINE_PROJECTList||!this.worlplanlist[i].WORLPLANLINE_BASISList||this.worlplanlist[i].WORLPLANLINE_PROJECTList.length == 0||this.worlplanlist[i].WORLPLANLINE_BASISList.length == 0){
									this.$message({
										message: '',
										type: 'warning'
									});
									return false;
								}else{
									if(!this.isEditList){
										// if(typeof(this.WORKPLAN.PROP_UNITDesc) != 'undefined') {
										// 	console.log(4444445555);
										// 	console.log(this.selectData);
										// 	for(var j=0;j<this.selectData.length;j++){
										// 		console.log(this.selectData[j].fullname);
										// 		console.log(this.selectData[j].id);
										// 		console.log(this.WORKPLAN.PROP_UNITDesc);
										// 		if(this.WORKPLAN.PROP_UNITDesc == this.selectData[j].fullname){
										// 			this.WORKPLAN.PROP_UNIT = this.selectData[j].id;
										// 		}
										// 	}
										// 	console.log('======');
										// 	console.log(this.WORKPLAN.PROP_UNIT);	
										// 	console.log('======');	
										// }
										// this.WORKPLAN.PROP_UNIT = this.WORKPLAN.PROP_UNITDesc;
										this.WORKPLAN.WORLPLANLINEList = this.worlplanlist;
										var url = this.basic_url +'/api-apps/app/workplan/saveOrUpdate';
										this.$axios.post(url, this.WORKPLAN).then((res) => {
											if(res.data.resp_code == 0) {
												if(opt == 'docUpload'){
													this.docParm.recordid = res.data.datas.id;
													this.docParm.model = 'edit';
													this.$refs.docTable.autoLoad();
													this.WORKPLAN.ID = res.data.datas.id;
												}else{
													this.$message({
														message: '',
														type: 'success'
													});
													this.show = false;
													this.$emit('request');
												}
											}else{
                                                this.$message({
                                                    message: res.data.resp_msg,
                                                    type: 'error'
                                                });
                                            }
										}).catch((err) => {
											this.$message({
												message: '',
												type: 'error'
											});
										});
										this.falg=true;
									}else{
										this.$message({
											message: '',
											type: 'warning'
										});
										return false;
									}
								}
							}
						}else{
							this.$message({
								message: '',
								type: 'warning'
							});
							return false;
						}				
					} else {
						this.show = true;
						this.$message({
							message: '',
							type: 'warning'
						});
						this.falg = false;
					}
				});	
			},
			reset(){
				this.WORKPLAN = {
					'ID': '',
					'WP_NUM': '',
					'DESCRIPTION': '',
					'YEAR': year,	
					'TYPE': '',
					'STATUS': '1',
					'LEADER_STATUS': '1',
					'STATUSDATE': date,
					'ITEMTYPE': '',
					'PROP_UNIT': '',
					'ENTERBY': '',
					'ENTERDATE': date,
					'CHANGEBY': '',
					'CHANGEDATE': '',
					'COMPACTOR': '',
					'C_PERSON': '',
					'APPRPERSON': '',
					'REPORTDATE': date,
					'MEMO': '',
					'MESSSTATUS': '1',
					'SYNCHRONIZATION_TIME': ''
				};
				this.worlplanlist = []; //
				// this.basisList = []; //
				// this.proTestList = []; //
				// this.isEditList = false;
			},
			//
			saveAndUpdate(WORKPLAN) {
				this.save(WORKPLAN);
				if(this.falg){
					this.show = false;
				}else{
					this.show = true;
				}
			},
			//
			saveAndSubmit(WORKPLAN) {
				this.save(WORKPLAN);
				// this.$emit('reset');
				this.reset();
				// this.show = true;
			},
			loadMore () {
			   if (this.loadSign) {
			     this.loadSign = false
			     this.page.currentPage++
			     if (this.page.currentPage > Math.ceil(this.page.totalCount/this.page.pageSize)) {
			       return
			     }
			    setTimeout(() => {
			       this.loadSign = true
			     }, 1000)
				//  this.requestData()
				if(this.requestnum == '1'){
					this.requesCategory();
				}else if(this.requestnum == '2'){
					this.requestProname();
				}else if(this.requestnum == '3'){
					this.requestDeptname();
				}else if(this.requestnum == '4'){
					this.requestBasis();
				}else if(this.requestnum == '5'){
					this.requestProject();
				}
			   }
			 },
			sizeChange(val) {
				this.page.pageSize = val;
				if(this.requestnum == '1'){
					this.requesCategory();
				}else if(this.requestnum == '2'){
					this.requestProname();
				}else if(this.requestnum == '3'){
					this.requestDeptname();
				}else if(this.requestnum == '4'){
					this.requestBasis();
				}else if(this.requestnum == '5'){
					this.requestProject();
				}
			},
			currentChange(val) {
				this.page.currentPage = val;
				if(this.requestnum == '1'){
					this.requesCategory();
				}else if(this.requestnum == '2'){
					this.requestProname();
				}else if(this.requestnum == '3'){
					this.requestDeptname();
				}else if(this.requestnum == '4'){
					this.requestBasis();
				}else if(this.requestnum == '5'){
					this.requestProject();
				}
			},
			searchinfo(index) {
				this.page.currentPage = 1;
				this.page.pageSize = 10;
				if(this.requestnum == '1'){
					this.requesCategory();
				}else if(this.requestnum == '2'){
					this.requestProname();
				}else if(this.requestnum == '3'){
					this.requestDeptname();
				}else if(this.requestnum == '4'){
					this.requestBasis();
				}else if(this.requestnum == '5'){
					this.requestProject();
				}
			},
			// requestData(index) {//
			// 	var data = {
			// 		page: this.page.currentPage,
			// 		limit: this.page.pageSize,
			// 		S_NUM: this.searchList.S_NUM,
			// 		S_NAME: this.searchList.S_NAME,
			// 		VERSION: this.searchList.VERSION,
			// 		DEPARTMENT: this.searchList.DEPARTMENT,
			// 		RELEASETIME: this.searchList.RELEASETIME,
			// 		STARTETIME: this.searchList.STARTETIME,
			// 		STATUS: this.searchList.STATUS,
			// 		P_NUM: this.searchList.P_NUM,
			// 		DEPARTMENT: this.searchList.DEPARTMENT,
			// 		P_NAME: this.searchList.P_NAME,
			// 		VERSION: this.searchList.VERSION,
			// 		STATUS: this.searchList.STATUS,
			// 	};
			
			
			// },
			handleClose(done) {
				this.$confirm('')
					.then(_ => {
						done();
					})
					.catch(_ => {});
			}
		},
		mounted() {
			// this.requestData();
			this.getCompany();
		},
	}
</script>

<style>
	@import '../../assets/css/mask-modules.css';
	.ml60 {margin-left: 60px;}
	#cost{text-align: right !important;}
	#costshow{float: right;}
	.el-dialog__footer {
		padding: 19px 20px 20px;
		text-align: center;
		-webkit-box-sizing: border-box;
		box-sizing: border-box;
	}
</style>


